.feedback-form {
  width: 280px;
  margin: 0 auto;
  margin-bottom: 24px;

  @media (min-width: @tablet-width) {
    width: 648px;
    display: flex;
    flex-wrap: wrap;
  }

  @media (min-width: @desktop-width) {
    width: 960px;
  }
}

.feedback-form__group {
  padding: 0;
  margin: 0;
  padding-bottom: 24px;
  margin-bottom: 24px;
  border: none;
  border-bottom: #e5e5e5 1px solid;

  @media (min-width: @tablet-width) {
    border: none;
  }
}

.feedback-form__send-button {
  width: 280px;
  height: 58px;
  background-color: #81b3d2;
  color: #ffffff;
  border: none;
  font-weight: 700;
  font-size: 21px;
  line-height: 26px;
  padding: 0;
  cursor: pointer;

  @media (min-width: @tablet-width) {
    width: 324px;
    margin-right: auto;
  }

  @media (min-width: @desktop-width) {
    width: 384px;
    margin-left: 10%;
  }
}

.feedback-form__send-text {
  margin: 0;
  font-size: 12px;
  line-height: 21px;
  color: rgba(51, 51, 51, 0.6);
  display: flex;
  align-items: center;
  justify-content: right;

  @media (min-width: @tablet-width) {
    color: #333333;
    font-size: 14px;
  }

  @media (min-width: @tablet-width) {
    margin-right: 10%;
  }
}
